import { ref, onMounted } from 'vue'
import { uploadImage } from '@/api/upload'
import Modal from 'bootstrap/js/dist/modal'
import Message from '@/components/library/Message'

//  hook
export const useUpload = () => {
  const uploadInputRef = ref(null)
  const file = ref(null)

  const getUploadFile = () => {
    file.value = uploadInputRef.value.files[0]
  }

  const upload = async () => {
    // 
    if (!uploadInputRef.value.files[0]) return Message({ text: '' })

    const formData = new FormData()
    formData.append('file-to-upload', file.value)

    const data = await uploadImage(formData)
    if (data.success) {
      Message({ type: 'success', text: '' })
      return data.imageUrl
    }
    Message({ type: 'error', text: ' jpgjpeg  png ' })
  }

  return {
    uploadInputRef,
    file,
    getUploadFile,
    upload
  }
}

// bsModal hook
export const useBsModal = (RefImpl) => {
  let bsModal = null
  onMounted(() => {
    bsModal = new Modal(RefImpl.value)
  })
  const openModal = () => {
    bsModal.show()
  }
  const closeModal = () => {
    bsModal.hide()
  }

  return {
    openModal,
    closeModal
  }
}
